export default function Historia () {
    return (
        <div className="historia">
            <div className="titulosHist">
                <h2 className="h2Historia">Nuestra</h2>
                <h1 className="h1Historia">Historia</h1>
            </div>
            <div className="parrafos">
                <div className="historiaP1"><p>Fundado en 1959, cuenta 3 restaurantes en Mxico, donde lo esperamos para que comparta con nosotros la pasin por la cocina internacional. En Sucursales encontrar las direcciones, telfonos y horarios de servicio de cada uno de nuestros restaurantes. Nuestra meta es permitir que nuestros clientes compartan el placer de la preparacin de nuestros extraordinarios platillos, haciendo que su visita se convierta en una experiencia inolvidable.</p></div>
                <div className="historiaP1"><p>Te hacemos sentir como en casa, con un servicio inigualable que nos ha caracterizado por ms de 60 aos. La amabilidad y profesionalismo de nuestro equipo de servicio, harn que tu estancia en cualquier a de nuestros restaurantes sea la mejor experiencia.</p></div>
                <div className="historiaP3"><p>Al frente de las cocinas de Picabira hay un equipo de chef's de un sazn inigualable. Desde muy temprano, comienzan la deliciosa preparacin de lo que se servir en el da.</p></div>
            </div>
        </div>
    )
}
